import React from "react"
import { Link } from "gatsby"

import { rhythm, scale } from "../utils/typography"

class Layout extends React.Component {
  render() {
    const { location, title, children } = this.props
    const rootPath = `${__PATH_PREFIX__}/`
    let header

    if (location.pathname === rootPath) {
	document.body.style = 'background: #fbfbfe;';
      header = (

        <h1
          style={{
            ...scale(1.0),
            fontFamily: `Lato`,
            marginBottom: rhythm(2.5),
            marginTop: 0,

          }}
        >
          <Link
            style={{
              boxShadow: `none`,
              fontFamily: `Lato`,
              fontWeight: 600,
              textDecoration: `none`,
              color: `#333d40`,
            }}
            to={`/`}
          >
            {title}
          </Link>
        </h1>
      )
    } else {
      header = (
        <h3
          style={{
            fontFamily: `Lato`,
            marginTop: 0,
          }}
        >
          <Link
            style={{
              fontFamily: `Lato`,
              boxShadow: `none`,
              textDecoration: `none`,
              color: `#333d40`,
            }}
            to={`/`}
          >
            {title}
          </Link>
        </h3>
      )
    }
    return (
      <div
        style={{
          fontFamily: `Lato`,
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(24),
	  color: `#333d40`,
          fontWeight: 250,
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
        }}
      >
        <header>{header}</header>
        <main>{children}</main>
        <footer style={{ boxShadow: `none`,color: `#333d40`,fontWeight: 200,fontSize: 11 }}>
          Â© {new Date().getFullYear()}, Built with
          {` `}
          <a  style={{ boxShadow: `none`,color: `#333d40`,fontWeight: 300, }} href="https://www.gatsbyjs.org">Gatsby</a>
        </footer>

      </div>
    )
  }
}

export default Layout
